@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Lato:wght@400;700&display=swap');

:root {
  --primary-font: 'Montserrat', Arial, sans-serif;
  --secondary-font: 'Lato', Arial, sans-serif;
}

:root {
  /* Primary Color */
  --primary: #9757D7;
  --primary-light: #B687F5;
  --primary-dark: #7432B1;
  
  /* Accent Colors */
  --accent-1: #FF9933;
  --accent-1-light: #FFC87A;
  --accent-1-dark: #CC7A27;
  
  --accent-2: #1BE8E7;
  --accent-2-light: #6AFFFF;
  --accent-2-dark: #14B6B3;
  
  /* Greys */
  --grey-100: #F5F5F5;
  --grey-200: #EEEEEE;
  --grey-300: #E0E0E0;
  --grey-400: #BDBDBD;
  --grey-500: #9E9E9E;
  --grey-600: #757575;
  --grey-700: #616161;
  --grey-800: #292929;
  --grey-900: #151515;
  --black: #000000;
}

/* Example usage in CSS selectors */
body {
  font-family: var(--secondary-font);
  line-height: 1.6;
  color: var(--grey-800);
  background-color: var(--grey-100);
}

h2, h3, h4, h5, h6 {
  font-family: 'Montserrat', Arial, sans-serif;
}

h1{
  font-family: 'Montserrat', Arial, sans-serif;
  color: var(--primary);
}

h1, h2, h3, h4, h5, h6 {
  font-family: var(--primary-font);
  margin-bottom: 1rem;
}


h1 {
  font-size: 2.5rem;
  line-height: 1.2;
}

h2 {
  font-size: 2rem;
  line-height: 1.25;
}

p {
  margin-bottom: 1.5rem;
}

a {
  font-family: 'Montserrat', Arial, sans-serif;
  color: var(--primary);
  text-decoration: none;
}

.button-primary {
  background-color: var(--primary);
  color: #ffffff;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  border: none;
  cursor: pointer;
}

.button-accent-1 {
  background-color: var(--accent-1);
  color: #ffffff;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  border: none;
  cursor: pointer;
}

.button-accent-2 {
  background-color: var(--accent-2);
  color: #ffffff;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  border: none;
  cursor: pointer;
}

.alert {
  background-color: var(--accent-1-light);
  color: var(--accent-1-dark);
  padding: 1rem;
  border-radius: 4px;
}

.site-logo {
  width: 50px;
  height: 50px;
}

.btn-primary {
  background-color: var(--primary-dark);
  border: var(--primary-dark);
}

.btn-primary:hover {
  background-color: var(--primary-light);
  border: var(--primary);
}

hr {

  color: var(--primary-light);
}

.megabold {
  font-weight: 700;
}

#heromain {
  font-size: 4rem;
  margin-top: 1.5rem;
}

#footer {
  background-color: black;
  margin-bottom: 0 !important;
  padding-bottom: 0;
}

.footer-text {
  color: white;
  font-size: 0.8rem;
}

.whitehover {
  /* For <a> elements - show as white instead of primary color */
  color: var( --grey-500);
}

.whitehover:hover {
  color: var(--primary-light)
}

