.hero-container {
    position: relative;
    width: 100%;
    height: 500px;
  }
  
  .background-video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
  }
  
  .hero-text {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    color: white;
    padding: 20px;
  }
  
  .secondary-text {
    font-size: 1.5rem;
    font-weight: bold;
    color: white;
  }
  
  .tertiary-text {
    font-size: 1rem;
    color: white;
  }
  
  .main-text {
    font-size: 3rem;
    margin-top: 1.5rem;
    color: white;
  }
  
  .additional-content {
    padding: 20px;
    margin-top: 20px;
  }
  
  .center-aligned-text {
    text-align: center;
    font-size: 2rem;
    font-weight: bold;
  }
  
  .center-aligned-paragraph {
    text-align: center;
    font-size: 1rem;
  }